@charset "UTF-8";
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  color: white;
  font-family: "Quicksand", sans-serif;
}

img {
  width: 100%;
  display: block;
}

.container {
  width: 85%;
  max-width: 160rem;
  margin: 0 auto;
  font-size: 1.6rem;
}

.span-orange {
  color: #E57979;
}

.d-n {
  display: none !important;
}

.ripple {
  position: absolute;
  background-color: #d62121;
  border-radius: 50%;
  pointer-events: none;
  animation: animateRippler 1s linear infinite;
  transform: translate(-50%, -50%);
}

.hidden {
  display: none;
}

.tienda-seleccionado {
  background-color: #E57979 !important;
  color: white !important;
}

.community-p {
  text-decoration: underline;
  text-align: center;
  margin: 0;
  font-size: 2rem;
}

.subrayado {
  background-color: rgba(212, 212, 212, 0.596);
}

.comentario-votado {
  color: #1D3557 !important;
}

.darken-error {
  color: #dd4e4e !important;
}

@keyframes animateRippler {
  0% {
    width: 0px;
    height: 0px;
  }
  100% {
    width: 0px;
    height: 0px;
  }
}
.loader {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background-color: white;
}
.loader .wrapper-dots {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
}
.loader .wrapper-dots .dot {
  width: 2.5rem;
  height: 2.5rem;
  border-radius: 50%;
  background-color: #E57979;
  animation: dots 1.2s infinite ease-in-out;
}
.loader .wrapper-dots .dot:not(:last-child) {
  margin-right: 1rem;
}
.loader .wrapper-dots .dot:nth-child(2) {
  animation-delay: 0.2s;
}
.loader .wrapper-dots .dot:nth-child(3) {
  animation-delay: 0.4s;
}
@keyframes dots {
  50% {
    opacity: 0;
    transform: scale(0.7) translateY(10px);
  }
}

.header {
  height: 24rem;
  background-color: #1D3557;
  width: 100%;
  box-sizing: border-box;
  display: flex;
  padding: 1rem 0;
}
.header .titulo-principal {
  text-decoration: none;
  color: white;
  text-align: center;
}
.header h1 {
  margin: 0;
  font-size: 2rem;
  font-weight: normal;
  grid-column: 2/3;
}
@media (min-width: 600px) {
  .header {
    align-items: center;
  }
  .header .titulo-principal {
    grid-column: 2/4;
  }
  .header h1 {
    margin-right: 1rem;
    grid-column: 2/4;
    place-self: center;
    font-size: 2.6rem;
  }
}
@media (min-width: 900px) {
  .header {
    height: 14rem;
  }
  .header h1 {
    font-size: 3.2rem;
  }
}

.header-container {
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-items: center;
}
@media (min-width: 600px) {
  .header-container {
    grid-template-columns: 1fr 1fr 1fr 1fr;
  }
}
@media (min-width: 900px) {
  .header-container {
    display: flex;
    justify-content: space-between;
    height: 7rem;
    align-items: center;
  }
}

.container-hamburguer {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0.5rem;
  place-self: start;
  padding: 0.5rem 0.8rem;
  grid-column: 1/2;
  grid-row: 1/2;
  align-self: center;
  cursor: pointer;
  border: 0.1rem solid #E57979;
  transition: 0.3s background ease-in-out;
}
.container-hamburguer i {
  font-size: 2.5rem;
  color: #E57979;
}
.container-hamburguer:hover {
  background-color: #E57979;
}
.container-hamburguer:hover i {
  color: white;
}
@media (min-width: 600px) {
  .container-hamburguer {
    place-self: start;
    align-self: center;
  }
  .container-hamburguer i {
    font-size: 3.2rem;
  }
}
@media (min-width: 900px) {
  .container-hamburguer {
    margin-right: 1rem;
  }
  .container-hamburguer i {
    font-size: 3.8rem;
  }
}

.button-comunidad {
  background-color: transparent;
  border: 0.1rem solid #E57979;
  border-radius: 0.5rem;
  grid-column: 1/4;
  width: 40%;
  min-width: 10rem;
  place-self: center;
  padding: 1rem 0;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.5s ease;
}
.button-comunidad img {
  width: 3.8rem;
  margin: 0 auto;
}
.button-comunidad span {
  font-size: 1.6rem;
}
.button-comunidad:hover {
  background-color: #e57979f6;
}
.button-comunidad:hover span {
  color: white;
}
@media (min-width: 600px) {
  .button-comunidad {
    grid-column: 2/4;
    place-self: center;
  }
}
@media (min-width: 900px) {
  .button-comunidad {
    width: 15rem;
    margin: 0 1rem 0 1rem;
  }
}

.wrapper-perfil {
  background-color: #E57979;
  position: relative;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  grid-column: 3/4;
  grid-row: 1/2;
  width: 4.5rem;
  place-self: end;
}
.wrapper-perfil.profile-icon-community {
  align-self: center;
  height: 80%;
}
@media (min-width: 900px) {
  .wrapper-perfil.profile-icon-community {
    align-self: initial;
    height: 100%;
  }
}
.wrapper-perfil .container-icon {
  cursor: pointer;
  text-decoration: none;
  color: white;
}
.wrapper-perfil i {
  font-size: 3rem;
}
@media (min-width: 600px) {
  .wrapper-perfil {
    width: 4rem;
    padding: 0.5rem 2.5rem;
    grid-column: 4/5;
    place-self: end;
  }
  .wrapper-perfil i {
    font-size: 3.6rem;
  }
}
@media (min-width: 900px) {
  .wrapper-perfil {
    place-self: center;
    width: 8rem;
    border-radius: 1rem;
    flex-direction: column;
  }
  .wrapper-perfil i {
    font-size: 4rem;
  }
}

.container-search {
  display: flex;
  position: relative;
  margin-top: 2rem;
  margin-bottom: 2rem;
  height: 3.5rem;
  grid-column: 1/4;
  flex-direction: column;
}
@media (min-width: 600px) {
  .container-search {
    grid-column: 1/5;
  }
}
@media (min-width: 900px) {
  .container-search {
    width: 50%;
    height: 4.5rem;
    align-items: center;
  }
}
.container-search .search-box {
  display: flex;
  flex-direction: column;
  background-color: white;
  height: 14rem;
  max-height: 14rem;
  position: absolute;
  width: 100%;
  border-radius: 1rem;
  left: 50%;
  top: 13rem;
  color: black;
  transform: translate(-50%, -50%);
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  overflow-y: scroll;
  z-index: 9;
}
.container-search .search-box .search-box-container {
  cursor: pointer;
  display: flex;
  align-items: center;
  flex: 1;
  text-decoration: none;
}
.container-search .search-box .search-box-container:not(:last-child) {
  border-bottom: 0.1rem solid black;
}
.container-search .search-box .search-box-container:hover {
  background-color: #f2f2f2;
  color: white;
}
.container-search .search-box .search-box-container-icon {
  margin-left: 1rem;
  color: #E57979;
}
.container-search .search-box .search-box-container-content {
  margin-left: 1rem;
  color: #1D3557;
}
.container-search .search-principal,
.container-search .search-comunidad {
  width: 100%;
  padding: 0 5rem 0 2rem;
  border-radius: 10rem 10rem 10rem 10rem;
  border: 0.2rem solid #E57979;
  font-size: 1.4rem;
  outline: none;
  height: 100%;
  font-family: "Quicksand", sans-serif;
  font-weight: 500;
}
@media (min-width: 600px) {
  .container-search .search-principal,
.container-search .search-comunidad {
    padding: 0 4.5rem 0 2rem;
  }
}
@media (min-width: 900px) {
  .container-search .search-principal,
.container-search .search-comunidad {
    padding: 0 4.5rem 0 2rem;
  }
}
.container-search .search-button {
  position: absolute;
  right: -0.1rem;
  bottom: 0;
  top: -0.3rem;
  width: 4.1rem;
  padding: 2rem 0;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #E57979;
  border: none;
  outline: none;
  color: white;
  cursor: pointer;
}
@media (min-width: 600px) {
  .container-search .search-button {
    width: 5.5rem;
    height: 5.5rem;
    top: -1rem;
  }
  .container-search .search-button i {
    font-size: 2rem;
  }
}
@media (min-width: 900px) {
  .container-search .search-button {
    top: -1rem;
    right: 0rem;
    width: 6.8rem;
    height: calc(100% + 2rem);
  }
  .container-search .search-button i {
    font-size: 2.5rem;
  }
}

.container-carousel {
  position: relative;
}

.glider-prev {
  top: 50% !important;
  left: -4rem !important;
  z-index: 0 !important;
  display: none !important;
}
.glider-prev img {
  width: 5rem;
}
@media (min-width: 600px) {
