  display: flex;
  align-items: center;
  justify-content: space-evenly;
  font-size: 1.5rem;
  color: white;
  flex-wrap: wrap;
  box-shadow: 0px 2px 0px 0px #e26868, 1.8px 2px 0px 0px #e36c6c;
}

.nombreProducto {
  width: 80%;
  margin: 0px auto;
  border-color: white;
  height: fit-content;
  overflow: hidden;
  font-size: 1.6rem;
}

.info {
  padding: 5px;
  font-size: 1.6rem;
  margin-bottom: 1rem;
}

.precio {
  padding-right: 5px;
  border-color: white;
  border-right: 0.1rem solid #E57979;
}

.title-section {
  margin-top: 5rem;
  background: linear-gradient(to right, #1D3557 0%, #457B9D 100%);
