}

.txtFooter {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 10px;
  margin-bottom: 2rem;
}
@media (max-width: 599px) {
  .txtFooter {
    flex-direction: column;
    align-items: center;
  }
}

.txt {
  font-size: 1.6rem;
  cursor: pointer;
  margin: 0 auto;
}
.txt a {
  text-decoration: none;
  color: white;
  font-size: 16px;
}
.txt a:hover {
  color: #E57979;
}

.marcaR {
  margin-top: 2rem;
}

#año {
  font-size: 1.4rem;
}

#mrhard {
  font-size: 1.4rem;
  color: #E57979;
}

.banner-logo {
  /* Rectangle 29 */
  margin-bottom: 2rem;
  height: 17rem;
  background: linear-gradient(270.07deg, rgba(168, 218, 220, 0.5) 27.1%, rgba(128, 179, 194, 0.422372) 40%, rgba(69, 123, 157, 0.31) 58.67%, rgba(69, 123, 157, 0.31) 78.44%);
  display: flex;
  align-items: center;
  justify-content: center;
}
.banner-logo img {
  min-width: 8rem;
  width: 20%;
  max-width: 20rem;
}
.banner-logo .text-banner {
  font-size: 1.6rem;
  width: 10rem;
  text-align: center;
  color: black;
  font-weight: lighter;
}
@media (min-width: 600px) {
  .banner-logo .text-banner {
    font-size: 2.4rem;
    width: 30rem;
  }
}
@media (min-width: 900px) {
  .banner-logo {
    height: 25rem;
  }
  .banner-logo img {
    margin-right: 2rem;
  }
  .banner-logo .text-banner {
    font-size: 3.2rem;
    width: 40rem;
  }
}

.wrapper-menuH {
  position: absolute;
  width: 100%;
  height: 100vh;
  z-index: 1;
  top: 0;
  left: 0;
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  display: flex;
  flex-direction: row;
}
.wrapper-menuH .menu-aside {
  height: 100%;
  width: 0;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: white;
  overflow-x: hidden;
  transition: 0.5s;
  position: relative;
}
.wrapper-menuH .menu-aside .cuerpoH {
  padding-top: 2rem;
  width: fit-content;
}
.wrapper-menuH .menu-aside .cuerpoH .tituloH {
  color: black;
  font-size: 2rem;
  padding-left: 2rem;
  margin-bottom: 1rem;
}
.wrapper-menuH .menu-aside .cuerpoH .conjuntoH {
  display: flex;
  flex-direction: row;
}
.wrapper-menuH .menu-aside .cuerpoH .conjuntoH:hover {
  cursor: pointer;
}
.wrapper-menuH .menu-aside .cuerpoH .conjuntoH:hover .irH {
  color: #E57979;
}
.wrapper-menuH .menu-aside .cuerpoH .conjuntoH:hover .opcionH {
  color: #E57979;
}
.wrapper-menuH .menu-aside .cuerpoH .conjuntoH .irH {
  color: black;
  margin-left: 18.6rem;
  font-size: 2rem;
  padding-top: 1.86rem;
  transition: 0.3s;
}
.wrapper-menuH .menu-aside .cuerpoH .conjuntoH .disparejo {
  margin-left: 19rem;
}
.wrapper-menuH .menu-aside .cuerpoH .opcionH {
  padding: 0.8rem 0.8rem 0.8rem 3.2rem;
  text-decoration: none;
  font-size: 1.6rem;
  color: #818181;
  display: block;
  transition: 0.3s;
  margin-top: 1rem;
  margin-bottom: 0.3rem;
  background: none;
  border: none;
}
.wrapper-menuH .menu-aside .cuerpoH .opcionH:hover {
  cursor: pointer;
  color: #E57979;
}
.wrapper-menuH .menu-aside .bienvenidaH {
  background-color: #1D3557;
  height: 7rem;
  font-size: 2.1rem;
  text-align: center;
  color: white;
  padding: 2rem;
}
.wrapper-menuH .menu-aside .bienvenidaH hr {
  border-color: #1D3557;
  border: none;
  height: 0.1rem;
  background-color: white;
  width: 80%;
}
.wrapper-menuH .menu-aside .footerH {
  position: absolute;
  bottom: 0;
  width: 100%;
  background-color: #1D3557;
  height: 5rem;
  font-size: 2.1rem;
  text-align: center;
  color: white;
  padding: 1.5rem;
}
.wrapper-menuH .menu-aside .footerH hr {
  border-color: #1D3557;
  border: none;
  height: 0.1rem;
  background-color: white;
}
.wrapper-menuH .close-hamburguer {
  position: relative;
  background-color: #E57979;
  color: white;
  left: 1rem;
  z-index: 1;
  top: 2rem;
  font-size: 2rem;
  height: 3.4rem;
  width: 3.4rem;
  text-align: center;
  padding-top: 0.6rem;
  border-radius: 5rem;
}
.wrapper-menuH .close-hamburguer:hover {
  cursor: pointer;
}

.mostrar-menuH {
  visibility: visible;
  opacity: 1;
  pointer-events: all;
  background-color: rgba(12, 12, 12, 0.507);
}
.mostrar-menuH .menu-principal {
  width: 32.6rem;
}

.quitar-menuH .menu-principal {
  width: 0;
}
.quitar-menuH .menu-hardware {
  width: 0;
}

.mostrar-menu-hard .menu-hardware {
  width: 32.6rem;
}

.quitar-menu-hardware .menu-hardware {
  width: 0;
}

.quitar-menu-perifericos .menu-perifericos {
  width: 0;
}

.mostrar-menu-perifericos .menu-perifericos {
  width: 32.6rem;
}

.menu-perifericos {
  width: 0;
}

.menu-hardware {
  width: 0;
}

.cuerpoH hr {
  border: none;
  height: 0.1rem;
  background-color: black;
}
.cuerpoH .volverH .volverH-icono {
  transform: rotate(180deg);
  color: black;
  margin-left: 1rem;
  font-size: 2rem;
  padding-top: 1.35rem;
  transition: 0.3s;
}
.cuerpoH .conjuntoH:hover {
  cursor: pointer;
}
.cuerpoH .conjuntoH:hover .subtitulo-opcion {
  color: #E57979;
}
.cuerpoH .conjuntoH:hover .volverH-icono {
  color: #E57979;
}
.cuerpoH .conjuntoH .subtitulo-opcion {
  font-size: 2.3rem;
  transition: 0.3s;
}

